body {
  font-family: "Codec Cold Trial";
}

.herosection {
  display: flex;
  align-items: center;
  position: relative;
  height: 650px;
  background: linear-gradient(-270.56deg, #f4f4f4 0.33%, #eee6e6 77.21%);
}
@media (max-width: 1024px) {
  .herosection {
    height: 580px;
  }
}
.herosection_page-title {
  margin-top: 75px;
}
.herosection .row {
  margin-top: 0;
  margin-bottom: 0;
}
.herosection_image {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.herosection_column {
  max-width: 50%;
  padding: 0;
}
@media (max-width: 768px) {
  .herosection_column {
    max-width: calc(100% - 120px);
  }
}
.section-1 {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.promo-section_spot {
  position: absolute;
  max-width: 130%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
.promo-card-wrapper {
  top: 0;
  left: 0;
  width: 100%;
  height: 416px;
}
.promo-card {
  padding: 30px;
  border-radius: 20px;
  box-shadow: var(--big-shadow);
  font-size: 14px;
  line-height: 24px;
  background-color: white;
  background-size: cover;
  color: var(--color-secondary);
}
.promo-card_1 {
  margin-top: 50px;
  max-width: 407px;
  margin-left: auto;
  background-image: url("../assets/images/index-page_section-1_card-1-background.svg");
}
.promo-card_1 a {
  display: inline-block;
  margin-top: 14px;
}
.promo-card_2 {
  max-width: 299px;
  margin-top: 50px;
  background-image: url("../assets/images/index-page_section-1_card-2-background.svg");
}
.open-test_button-wrapper {
  display: flex;
  justify-content: flex-start;
}
@media (max-width: 1024px) {
  .promo-card_1 {
    max-width: 323px;
  }
}
@media (max-width: 425px) {
  .promo-section_spot {
    transform: translate(-50%, -45%);
  }
  .promo-card-wrapper {
    height: auto;
  }
  .promo-card_1 {
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
  }
  .promo-card_2 {
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
  }
  .open-test_button-wrapper {
    justify-content: center;
  }
}
.pose-cards-wrapper {
  position: relative;
}
.pose-cards-stack {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(4, 1fr);
  position: absolute;
  top: 0;
}
.pose-cards-spaceholder {
  height: 204px;
  width: 100%;
}

.pose-card {
  display: flex;
  flex-direction: column;
  margin: 0;
  position: relative;
  overflow: hidden;
  width: 156.3px;
  background: rgba(249, 249, 249, 0.8);
  box-shadow: var(--big-shadow);
  border-radius: 20px;
  height: 202px;
}

.pose-card::after {
  content: "";
  display: block;
  position: absolute;
  top: 11.51px;
  right: 11.51px;
  width: 35px;
  height: 35px;
  background: #ffffff;
  box-shadow: var(--small-shadow);
  border-radius: 50%;
  background-image: url("../assets/images/icon-info.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.pose-card_bg {
  max-width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}
.pose-card_image {
  display: block;
  position: absolute;
  max-width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}
.pose-card_title {
  font-size: 14px;
  line-height: 14px;
  margin-top: auto;
  margin-bottom: 14px;
  width: 100%;
  text-align: center;
}
/* special *magic* case for smaller image */
.pose-card:first-child .pose-card_image {
  max-width: 122%;
  transform: translate(-50%, -56%);
}

.features-list-wrapper {
  margin-top: 60px;
}
.features-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin: 0;
  padding: 0;
}
.fetaures-list_item {
  margin: 0;
  padding: 0;
  display: flex;
  align-content: center;
  justify-content: flex-start;
  font-size: 18px;
  font-weight: bold;
  line-height: 24px;
  color: var(--color-secondary);
  letter-spacing: -0.03em;
}
.fetaures-list_icon {
  height: 44px;
  width: 44px;
  min-height: 44px;
  min-width: 44px;
  border-radius: 50%;
  box-shadow: var(--small-shadow);
  background: var(--color-primary);
  margin-right: 12px;
  background-repeat: no-repeat;
}
.fetaures-list_icon__heart {
  background-image: url("../assets/images/index-page_feature-item_heart.svg");
  background-size: 22px 20px;
  background-position: 11px 13px;
}
.fetaures-list_icon__pose1 {
  background-image: url("../assets/images/index-page_feature-item_pose1.svg");
  background-size: 22.46px 19.88px;
  background-position: 12.3px 11px;
}
.fetaures-list_icon__script {
  background-image: url("../assets/images/index-page_feature-item_script.svg");
  background-size: 22px 19px;
  background-position: 11px 13px;
}
.fetaures-list_icon__pose2 {
  background-image: url("../assets/images/index-page_feature-item_pose2.svg");
  background-size: 22px 21px;
  background-position: 11px 12px;
}
@media (max-width: 530px) {
  .fetaures-list_item {
    font-size: 12px;
    line-height: 18px;
  }
}
@media (max-width: 425px) {
  .fetaures-list_item {
    font-size: 12px;
    line-height: 18px;
  }
  .fetaures-list_icon {
    height: 36px;
    width: 36px;
    min-height: 36px;
    min-width: 36px;
  }
  .fetaures-list_icon__heart {
    background-size: 18px 16.32px;
    background-position: 9px 10.64px;
  }
  .fetaures-list_icon__pose1 {
    background-size: 18.37px 16.27px;
    background-position: 10.06px 9px;
  }
  .fetaures-list_icon__script {
    background-size: 18px 15.55px;
    background-position: 9px 10.64px;
  }
  .fetaures-list_icon__pose2 {
    background-size: 18px 17.18px;
    background-position: 9px 9.82px;
  }
}

.section-5 {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 425px) {
  .section-5 {
    margin-top: 40px;
    margin-bottom: 40px;
  }
}

.app-interface_spot {
  width: 90%;
  margin-left: auto;
}

.app-interface_screenshot {
  position: absolute;
  width: 485px;
  max-width: 100%;
  left: 0;
}
@media (max-width: 425px) {
  .app-interface_screenshot {
    max-width: 85%;
  }
}

.app-daily-card-wrapper {
  position: relative;
}
.app-daily-card-space-holder {
  height: 308px;
}
.app-daily-card-stack {
  position: absolute;
  top: 0;
  display: grid;
  gap: 30px;
  grid-template-columns: 1fr 1fr;
}
.app-daily-card {
  position: relative;
  width: 340px;
  background: white;
  border-radius: 20px;
  overflow: hidden;
  letter-spacing: -0.03em;
  box-shadow: var(--big-shadow);
}
.app-daily-card_label {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 133px;
  left: 20px;
  padding: 6px 10px 4px 10px;
  background: #ffffff;
  border-radius: 20px;
  font-size: 10px;
  line-height: 10px;
  color: black;
}
.app-daily-card_cover {
  height: 165px;
  width: 100%;
  object-fit: cover;
}
.app-daily-card_content {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px 20px 20px;
  height: 135px;
}
.app-daily-card_title {
  margin: 0;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: -0.03em;
  color: #000000;
}
.app-daily-card_title .font-fallback {
  font-weight: 400;
}
.app-daily-card_text {
  margin: 14px 0 0 0;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.03em;
  color: #787878;
}
.app-daily-cards-section_spot {
  max-width: 150%;
  position: absolute;
  z-index: -1;
}
@media (max-width: 425px) {
  .app-daily-card {
    width: 242px;
  }
  .app-daily-card_content {
    padding: 12px 14px 16px 14px;
    height: auto;
  }
  .app-daily-card_cover {
    height: 110px;
  }
  .app-daily-card_label {
    top: 84px;
    left: 15px;
  }
  .app-daily-card_title {
    font-size: 14px;
    line-height: 18px;
  }
  .app-daily-card_text {
    margin-top: 6px;
    font-size: 12px;
    line-height: 16px;
  }
}

.app-mockup-profile-section_spot {
  position: absolute;
  right: 0;
  top: 50%;
  max-width: 145%;
  transform: translateY(-45%);
  z-index: -1;
}
.app-mockup-profile-section_image {
  display: block;
  margin-left: auto;
  max-width: 427px;
  width: calc(100% - 100px);
}
@media (max-width: 1024px) {
  .app-mockup-profile-section_image {
    width: calc(100% - 50px);
  }
}

.stats-section_map-image {
  z-index: -1;
  position: absolute;
  transform: translateX(100px);
}
.stats-section_stats-wrapper {
  display: flex;
  height: 426px;
}
@media (max-width: 1024px) {
  .stats-section_stats-wrapper,
  .stats-section_map-image {
    height: 280px;
  }
}
.stats-section_stats-wrapper_column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 30px;
}
.stats-section_stats-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 17px;
  box-sizing: border-box;
  border-radius: 20px;
  box-shadow: var(--big-shadow);
  width: 159.57px;
  height: 85.11px;
  background-color: white;
  margin-bottom: 20px;
}
.stats-section_stats-card:last-child {
  margin-bottom: 0;
}
.stats-section_stats-card_title {
  font-size: 12px;
  line-height: 18px;
  text-align: center;
  letter-spacing: -0.03em;
  color: #787878;
}
.stats-section_stats-card_value {
  position: relative;
  font-family: "Poppins", sans-serif;
  font-size: 28px;
  font-weight: 400;
  line-height: 42px;
  display: flex;
  align-items: center;
  letter-spacing: -0.03em;
  color: #484851;
}
.stats-section_stats-card_value_icon {
  display: inline-block;
  margin-right: 11px;
}
.app-consultation-feature_spot {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  z-index: -1;
}
@media (max-width: 768px) {
  .app-consultation-feature_spot {
    top: 50%;
    left: 50%;
    right: auto;
    transform: translate(-50%, -50%);
  }
}
.app-consultation-feature_image {
  display: block;
  max-height: 571px;
  margin: 0 auto;
}

.app-consultation-feature_card {
  position: absolute;
  top: 45%;
  width: 304px;
  right: 10%;
  padding: 16px 24px 16px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: var(--big-shadow);
  border-radius: 20px;
  background: white;
  max-width: 430px;
}
.app-consultation-feature_card_icon {
  height: 67px;
  width: 67px;
  min-width: 67px;
  min-height: 67px;
  background: #eee6e6;
  background-image: url("../assets/images/icon-hetero.svg");
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;
  margin-right: 17px;
}
.app-consultation-feature_card_text {
  width: 100%;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: -0.03em;
}
.app-consultation-feature_card_arrow {
  height: 12px;
  width: 20px;
  background-image: url("../assets/images/icon-arrow.svg");
  background-repeat: no-repeat;
  background-size: contain;
}
@media (max-width: 480px) {
  .app-consultation-feature_card {
    max-width: 200px;
    font-size: 14px;
    line-height: 18px;
    right: 0;
  }
  .app-consultation-feature_card_icon {
    height: 40px;
    width: 40px;
    min-width: 40px;
    min-height: 40px;
    background-size: 22px 16px;
  }
}
.swiper-container {
  width: 100%;
  max-width: 100%;
}
.swiper-pagination {
  margin-top: 40px;
}
.swiper-pagination-bullet {
  margin: 0 7px !important;
  width: 10px !important;
  height: 10px !important;
  opacity: 1 !important;
  background-color: #eee6e6 !important;
}
.swiper-pagination-bullet-active {
  background-color: #f1d8ce !important;
}
.expert-review {
  margin-bottom: 40px;
}
.expert-review_image-block {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-bottom: 32px;
}
.expert-review_image {
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 192px;
  width: 192px;
  box-shadow: var(--big-shadow);
  border-radius: 50%;
}
.expert-review_name {
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  letter-spacing: -0.03em;
  color: var(--color-secondary);
}
.expert-review_label {
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  letter-spacing: -0.03em;
  color: var(--color-secondary);
  margin-bottom: 20px;
}
.expert-review_label .font-fallback {
  font-weight: 400;
}

.footer,
.footer .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #eee6e6;
}
.footer {
  padding: 20px 0 90px 0;
}

.footer h1 {
  color: #484851;
  margin: 0;
  text-align: center;
}
.footer .cta-form {
  margin-top: 40px;
  max-width: 100%;
}
.footer-wave {
  display: block;
  width: 100%;
}
.footer_app-links-wrapper {
  display: flex;
  align-items: center;
  margin-top: 40px;
}
.footer_app-store-link {
  transition: 0.2s ease-in-out;
}
.footer_app-store-link:focus,
.footer_app-store-link:hover {
  outline: none;
  transform: scale(1.02);
}
.footer_app-store-logo {
  display: block;
  width: 200px;
  max-width: calc(100% - 14px);
  margin-left: 7px;
  margin-right: 7px;
}

.footer_links {
  margin-top: 79px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer_links-vertical-divider {
  display: inline-flex;
  height: 1em;
  width: 1px;
  background: var(--color-secondary);
  margin: 0 20px;
}
.footer_copyright-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: normal;
  font-size: 14px;
  letter-spacing: -0.03em;
  color: var(--color-secondary);
}

@media (max-width: 768px) {
  .footer_links-vertical-divider {
    display: none;
  }
  .footer_copyright-wrapper,
  .footer_links {
    flex-direction: column;
  }
  .footer_copyright-wrapper {
    margin-bottom: 36px;
  }
}

.footer_social-wrapper {
  display: flex;
  align-items: center;
}
.footer_social-button {
  display: block;
  height: 50px;
  width: 50px;
  min-width: 50px;
  min-height: 50px;
  box-shadow: var(--small-shadow);
  border-radius: 50%;
  background-color: white;
  margin-right: 10px;
  margin-left: 10px;
  background-repeat: no-repeat;
}
.footer_social-button:first-child {
  margin-left: 0;
}
.footer_social-button:last-child {
  margin-right: 0;
}
.footer_social-button__instagram {
  background-image: url("../assets/images/icon-ig.svg");
  background-position: center center;
}
.footer_social-button__facebook {
  background-image: url("../assets/images/icon-fb.svg");
  background-position: center center;
}
.footer_social-button__vk {
  background-image: url("../assets/images/icon-vk.svg");
  background-position: center center;
}
.footer_copyright-wrapper .font-fallback {
  font-weight: 400 !important;
}
.footer_links-copyright_height-keeper {
  overflow: hidden;
  opacity: 0;
  width: 0;
}
@media (max-width: 768px) {
  .footer_links-copyright_height-keeper {
    display: inline-block;
  }
}
